.unlogin.screen {
  height: 100vh;
  overflow: hidden;
  text-align: center;
  background: #f7f7f7;
  overflow: hidden; }

@-webkit-keyframes unlogin-up {
  0%, 50%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  25% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px); }
  75% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px); } }

@keyframes unlogin-up {
  0%, 50%, 100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  25% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px); }
  75% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px); } }

.unlogin .page-inner {
  height: 100%; }

.unlogin .page {
  height: 100%;
  position: relative;
  overflow: hidden; }
  .unlogin .page:after {
    content: '';
    display: block;
    width: 52px;
    height: 20px;
    background: url("./up.svg") 100%/100%;
    position: absolute;
    bottom: 4%;
    left: calc(50% - 26px);
    -webkit-animation: unlogin-up 2s linear infinite;
            animation: unlogin-up 2s linear infinite; }
  .unlogin .page:first-child:after {
    background-image: url("./up-white.svg"); }
  .unlogin .page:last-child:after {
    content: normal; }

.unlogin.layer {
  position: absolute;
  left: 0;
  top: 0;
  background: #fff;
  width: 100%;
  height: 100%; }
  .unlogin.layer .close {
    position: absolute;
    right: 30px;
    top: 30px;
    padding: 10px; }
    .unlogin.layer .close:after {
      content: '';
      display: block;
      width: 32px;
      height: 32px;
      background: url("./close.svg") 100%/100%; }



